import emptyView from '../../components/emptyview.vue';

export default {
	data() {
		return {
			list: [],
			pageNum: 1,
			pageSize: 10,
			total: 1,
			time: 0,
			balanceType: ['购买', '消费', '退款','后台增加'],
			balanceSymbol: ['+', '-', '-','+']
		}
	},
	components: {
		emptyView
	},
	onLoad(options) {


	},
	onShow() {
		this.time = uni.getStorageSync("patientInfo").time;
		this.pageNum = 1;
		this.total = 1;
		this.list = [];
		this.getUserTime();
	},
	onReady() {

	},
	onReachBottom() {
		this.getUserTime();
	},

	methods: {
		getUserTime() {
			let that = this;
			if (that.total <= (that.pageNum - 1) * that.pageSize) {
				return;
			}
			const params = {};
			params.pageNum = that.pageNum;
			params.pageSize = that.pageSize;
			that.$util.request({
				url: "/didi-patient/patienttimeinfo/queryPage",
				param: params,
				success: function(res) {
					that.list = that.list.concat(res.data.list);
					that.pageNum += 1;
					that.total = res.data.total;
					for (var i = 0; i < that.list.length; i++) {
						that.list[i].createTime = that.list[i].createTime.split(' ')[0];
					}
				},
				error: function() {}
			})
		},
		toRefund:function(){
			uni.navigateTo({
				url:'/pages/my/userRefund?time='+this.time
			})
		}
	}
}
